import {
    Component,
    OnInit,
    Inject,
    ViewChild,
    AfterContentInit
} from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Title } from '@angular/platform-browser';
import { Subject } from 'rxjs';
import { debounceTime, finalize, switchMap, map } from 'rxjs/operators';
import { ToastrService } from 'ngx-toastr';
import { OfficeService } from './services/office.service';
import { Office } from './models/office.model';
import { NhTabComponent } from '../../../../shareds/components/nh-tab/nh-tab.component';
import { BaseListComponent } from '../../../../base-list.component';
import { IPageId, PAGE_ID } from '../../../../configs/page-id.config';
import { SpinnerService } from '../../../../core/spinner/spinner.service';
import { UtilService } from '../../../../shareds/services/util.service';
import { AppService } from '../../../../shareds/services/app.service';
import { OfficeSearchViewModel } from './models/office-search.viewmodel';
import { ISearchResult } from '../../../../interfaces/isearch.result';
import { OfficeFormComponent } from './office-form/office-form.component';
import { DestroySubscribers } from '../../../../shareds/decorator/destroy-subscribes.decorator';
import { CheckPermission } from '../../../../shareds/decorator/check-permission.decorator';
import { OfficeDetailComponent } from './office-detail/office-detail.component';
import { NhSuggestion } from '../../../../shareds/components/nh-suggestion/nh-suggestion.component';

@Component({
    selector: 'app-office',
    templateUrl: './office.component.html',
    providers: [OfficeService]
})
@DestroySubscribers()
@CheckPermission()
export class OfficeComponent extends BaseListComponent<OfficeSearchViewModel> implements OnInit {
    @ViewChild(NhTabComponent) tabComponent: NhTabComponent;
    @ViewChild(OfficeFormComponent) officeFormComponent: OfficeFormComponent;
    @ViewChild(OfficeDetailComponent) officeDetailComponent: OfficeDetailComponent;
    status?: number = null;
    isActive: boolean;

    // Test.
    data = [];

    constructor(@Inject(PAGE_ID) public pageId: IPageId,
                private route: ActivatedRoute,
                private router: Router,
                private title: Title,
                private spinnerService: SpinnerService,
                private utilService: UtilService,
                private officeService: OfficeService,
                private appService: AppService) {
        super();
    }

    ngOnInit(): void {
        this.appService.setupPage(
            this.pageId.HR,
            this.pageId.OFFICE,
            'Qun l phng ban',
            'Danh sch phng ban'
        );
        this.listItems$ = this.route.data.pipe(
            map((result: { data: ISearchResult<OfficeSearchViewModel> }) => {
                const data = result.data;
                this.totalRows = data.totalRows;
                return data.items;
            })
        );
        this.officeService.searchForSuggestion('')
            .subscribe((result: NhSuggestion[]) => this.data = result);
    }

    onSuggestionSearched(keyword: string) {
        console.log(keyword);
        this.officeService.searchForSuggestion(keyword)
            .subscribe((result: NhSuggestion[]) => this.data = result);
    }

    search(currentPage) {
        this.currentPage = currentPage;
        this.isSearching = true;
        this.listItems$ = this.officeService
            .search(
                this.keyword,
                this.isActive,
                this.currentPage,
                this.pageSize
            )
            .pipe(
                finalize(() => (this.isSearching = false)),
                map((result: ISearchResult<OfficeSearchViewModel>) => {
                    this.totalRows = result.totalRows;
                    return result.items;
                })
            );
    }

    refresh() {
        this.keyword = '';
    }

    add() {
        this.officeFormComponent.add();
    }

    edit(officeId: number) {
        this.officeFormComponent.edit(officeId);
    }

    detail(officeId: number) {
        this.officeDetailComponent.getDetail(officeId);
    }

    delete(officeId: number) {
        this.subscribers.deleteOffice = this.officeService.delete(officeId).subscribe(() => this.search(1));
    }

    onChangeActiveStatus(office: Office) {
        office.isActive = !office.isActive;

        // this.officeService.updateIsActive(office).subscribe((result: IActionResultResponse) => {
        //     // if (result === -1) {
        //     //     this.toastr.error(this.formatString(this.message.notExists, 'Phng ban'));
        //     //     return;
        //     // }
        //     //
        //     // if (result > 0) {
        //     //     this.toastr.success(`${office.isActive ? 'Kch hot' : 'B kch hot'} phng ban "${office.name}" thnh cng.`);
        //     //     return;
        //     // }
        //     //
        //     // if (result === 0) {
        //     //     this.toastr.warning('Vui lng thay i trng thi ca phng ban');
        //     //     return;
        //     // }
        //
        //     this.toastr.warning(result.message, result.title);
        //     return;
        // });
    }

    onTabClosed(data: { id: string; active: boolean }) {
        if (data.active) {
            this.search(this.currentPage);
            this.tabComponent.setTabActiveById('tabListOffice');
        }
    }

    createRange(number) {
        const items = [];
        for (let i = 1; i <= number; i++) {
            items.push(i);
        }
        return items;
    }
}
