import { Component, OnInit, ViewChild } from '@angular/core';
import { OfficeService } from '../services/office.service';
import { NhModalComponent } from '../../../../../shareds/components/nh-modal/nh-modal.component';
import { OfficeDetailViewModel } from '../models/office-detail.viewmodel';
import { DestroySubscribers } from '../../../../../shareds/decorator/destroy-subscribes.decorator';
import { IActionResultResponse } from '../../../../../interfaces/iaction-result-response.result';
import { SpinnerService } from '../../../../../core/spinner/spinner.service';
import { TreeData } from '../../../../../view-model/tree-data';
import { AppService } from '../../../../../shareds/services/app.service';
import { LanguageSearchViewModel } from '../../../../../shareds/models/language.viewmodel';

@Component({
    selector: 'app-office-detail',
    templateUrl: './office-detail.component.html'
})

@DestroySubscribers()
export class OfficeDetailComponent implements OnInit {
    @ViewChild('officeDetailModal') officeDetailModal: NhModalComponent;
    officeDetail: OfficeDetailViewModel;
    officeTree: TreeData[] = [];
    subscribers: any = {};
    languages = [];
    currentLanguage = '';

    constructor(private appService: AppService,
                private spinnerService: SpinnerService,
                private officeService: OfficeService) {
        this.languages = this.appService.languages.map((language: LanguageSearchViewModel) => {
            if (language.isDefault) {
                this.currentLanguage = language.languageId;
            }
            return {id: language.languageId, name: language.name, isSelected: language.isDefault};
        });
    }

    ngOnInit() {
    }

    onNodeSelected(node: TreeData) {
        this.getDetail(node.id);
    }

    getDetail(officeId: number) {
        this.spinnerService.show();
        this.subscribers.getTree = this.officeService.getTree()
            .subscribe((result: TreeData[]) => this.officeTree = result);
        this.subscribers.getDetail = this.officeService.getDetail(officeId)
            .subscribe((result: IActionResultResponse<OfficeDetailViewModel>) => {
                this.officeDetail = result.data;
                this.officeDetailModal.show();
            });
    }
}
